            <!-- 切换视图 -->
            <div id="fileManagerChangeView">
              <img src="/assets/apps/FileManager/list.svg" alt="列表"/>
              <img src="/assets/apps/FileManager/thumbnails.svg" alt="略缩图"/>
            </div>
          </div>

          Content
        </a-layout-content>
      </a-layout>
    </a-layout-content>
    <a-layout-footer style="text-align: center">
